<template lang="pug">
  div#joinus
    div.container
      h1.joinus__title.section__title {{ serviceKo }} 
      p.section__subtitle
        | {{ serviceKo }}    .
        br
        |      .
        br
        span.joinus__title--copyEmail(
          @click="toast"
          v-clipboard:copy="mailTo"
          v-tooltip.bottom="{content: '   ', delay: {show: 500, hide: 100}}"
        )   
          i.far.fa-copy

      div.global__cta
        a.global__cta--link(
          :href="`mailto:${mailTo}?subject= () `"
        )
          button.global__cta--btn.mailto(
            v-tooltip.bottom="{content: '  ( )', delay: {show: 500, hide: 100}}"
          )  

        a.global__cta--link(
          :href="sellerRequestUrl"
          target="_blank"
        )
          button.global__cta--btn.form(
            v-tooltip.bottom="{content: '( )', delay: {show: 500, hide: 100}}"
          ) 
</template>

<script>
import {globalVar} from '@/globalVar'

export default {
  name: 'joinus',

  data () {
    return {
      mailTo: globalVar.mailTo,
      serviceKo: globalVar.serviceKo,
      serviceEn: globalVar.serviceEn,
      baseURI: globalVar.ceoWebsiteUrl,
      companyMailTo: globalVar.companyMailTo,
      sellerRequestUrl: globalVar.sellerRequestUrl,
    }
  },

  methods: {
    toast () {
      this.$toasted.show('  ', {
        theme: "primary",
        position: "bottom-center",
        duration : 2000
      })
    }
  },

}
</script>

<style lang="scss">
#joinus {
  border-top: 1px solid $texteee;

  .container {
    .section__subtitle {
      margin-bottom: 0;

      .joinus__title--copyEmail {
        cursor: pointer;
        font-weight: 400;
        color: $brand-pink;
        display: inline-block;
        @include transition(all 0.25s ease);

        &:hover {
          opacity: 0.5;
        }

        .fa-copy {
          margin-left: $grid;
        }
      }
    }

    .global__cta {
      width: 100%;
      display: inline-block;

      .global__cta--btn {
        width: $grid40x;
        border: 1px solid $brand-pink;

        &.form {
          color: #fff;
          background-color: $brand-pink;
        }

        &.mailto {
          margin-right: $grid4x;

          @media #{$pablet} {
            display: none;
          }

          &:hover {
            color: $brand-pink;
            background-color: #fff;
          }
        }

        &:hover {
          opacity: 0.5;
          // color: $brand-pink;
          // background-color: #fff;
        }
      }
    }
  }
}
</style>



